 pieza a la izquierda es de forma irregular, probablemente una manga, con las flechas indicando la dirección del hilo paralelo al borde inferior. Las otras dos piezas, que parecen ser partes del cuerpo de una prenda, también tienen flechas indicando la dirección del hilo a lo largo del largo de las piezas. Cada pieza del patrón tiene un pequeño rectángulo blanco en el centro, que es una etiqueta o una marca de referencia."}})])]),_c('div',{staticClass:"row",attrs:{"titulo":"Sesgo o bies"}},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',{staticClass:"ps-5 p-3"},[_vm._v("Se llama sesgo o bies al sentido diagonal de las telas en un ángulo de 45° grados; diagonal o al sesgo aporta mejor caída y cierta elasticidad. En algunos casos es necesario cortar las piezas de una prenda al sesgo debido a su diseño.")])]),_c('div',{staticClass:"col-md-5"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/imagen19.png"),"alt":"La imagen ilustra la orientación de los patrones en la tela, mostrando las direcciones del recto hilo, el contrahílo y el bies. A la izquierda, se presenta un cuadrado de tela con las direcciones señaladas. A la derecha, se muestra cómo colocar los patrones en la tela, alineándolos según el recto hilo y el centro del pliegue para asegurar un corte preciso y adecuado. "}})])])]),_c('Separador'),_vm._m(24),_vm._m(25),_c('p',[_vm._v("Los anchos de las telas que se pueden encontrar normalmente en los puntos de venta tienen diferentes medidas que suelen estar normalizados. Estos anchos vienen de fábrica y se dividen en:")]),_vm._m(26),_vm._m(27),_c('br'),_vm._m(28),_vm._m(29),_c('p',[_vm._v("El propósito del orillo es que la tela no se deshilache. Según el diseño o fabricante los orillos de la tela se dividen en:")]),_c('div',{staticClass:"row mt-4 mb-4",attrs:{"data-aos":"zoom-in-left"}},[_c('div',{staticClass:"col-sm-12 col-md-7 col-lg-7"},[_c('LineaTiempoD',{staticClass:"color-primario"},[_c('div',{staticClass:"row",attrs:{"numero":"1","titulo"