eados con el recto hilo y el orillo de la tela para asegurar un corte preciso. A la derecha, se ilustra cómo una pieza del patrón debe estar alineada con el orillo de la tela para que salga completa y correctamente cortada. Las instrucciones enfatizan la importancia de seguir la dirección del recto hilo y el orillo para obtener un resultado adecuado. La imagen muestra cómo alinear y cortar patrones en la tela. A la izquierda, se ven patrones colocados sobre una tela, alineados con el recto hilo y el orillo de la tela para asegurar un corte preciso. A la derecha, se ilustra cómo una pieza del patrón debe estar alineada con el orillo de la tela para que salga completa y correctamente cortada. Las instrucciones enfatizan la importancia de seguir la dirección del recto hilo y el orillo para obtener un resultado adecuado. La imagen muestra cómo alinear y cortar patrones en la tela. A la izquierda, se ven patrones colocados sobre una tela, alineados con el recto hilo y el orillo de la tela para asegurar un corte preciso. A la derecha, se ilustra cómo una pieza del patrón debe estar alineada con el orillo de la tela para que salga completa y correctamente cortada. Las instrucciones enfatizan la importancia de seguir la dirección del recto hilo y el orillo para obtener un resultado adecuado. La imagen muestra cómo alinear y cortar patrones en la tela. A la izquierda, se ven patrones colocados sobre una tela, alineados con el recto hilo y el orillo de la tela para asegurar un corte preciso. A la derecha, se ilustra cómo una pieza del patrón debe estar alineada con el orillo de la tela para que salga completa y correctamente cortada. Las instrucciones enfatizan la importancia de seguir la dirección del recto hilo y el orillo para obtener un resultado adecuado. "}})])]),_c('div',{staticClass:"row justify-content-center my-lg-4"},[_c('p',[_c('b',[_vm._v("Tendido cerrado ")])]),_c('div',{staticClass:"col-md-10"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/tarjeta