lez de tela"}},[_c('div',{staticClass:"col-md-4 mb-4 mb-md-0"},[_c('p',{staticClass:"p-5"},[_vm._v("Se realiza cuando la tela se dobla a la mitad de su ancho, se une orillo con orillo formando un doblez de tela en un extremo, conocido como lomo. Al doblar la tela queda derecho con derecho, lo que se denomina careado o cara a cara. Este tipo de extendido se utiliza para cortar prototipos, es utilizado por modistas o sastres.")])]),_c('div',{staticClass:"col-md-8"},[_c('img',{staticClass:"p-5",attrs:{"src":require("@/assets/curso/temas/tema2/tarjeta7/imagen2.svg"),"alt":"La figura describe un diagrama en blanco y negro de varias capas de tela apiladas, indicando claramente el “Derecho de la tela”, el “Revés de la tela”, y el “Doblez de tela”. Las flechas señalan la “Dirección de la tela” y los “Orillos”, que son los bordes terminados. Cada capa está etiquetada con “dirección del tejido” y “derecho del tejido”. Es crucial extender la capa de tela según el largo del trazo y determinar cuál es el derecho de la tela para asegurar que todas las piezas se corten en la misma orientación, manteniendo la consistencia en la apariencia y el comportamiento del tejido en la prenda final. "}})])]),_c('div',{staticClass:"row borde-redondo-tmorada",attrs:{"titulo":"Extendido cara a cara"}},[_c('div',{staticClass:"col-md-4 mb-4 mb-md-0"},[_c('p',{staticClass:"p-5"},[_vm._v("La capa de tela se extiende según el largo del trazo, es importante determinar cuál es el derecho de la tela, ya que la primera capa debe quedar con el derecho hacia arriba con respecto a la mesa. Se extiende la capa desde el inicio del trazo hasta el final del largo del trazo y se procede a cortar.")]),_c('p',{staticClass:"ps-5"},[_vm._v("Se repite el proceso con la siguiente capa, pero teniendo en cuenta que esta debe quedar con el derecho de tela hacia abajo, quedando cada a cara con la primera capa. Las siguientes capas deben quedar en pares, derecho con derecho de la tela.")])]),_c('div',{staticClass:"col-md-8"},[_c('img',{staticClass:"p-5",attrs:{"src":require("@/assets/curso/temas/tema2/tarjeta7/imagen3.svg"),"alt":"La figura describir un diagrama en blanco y negro de varias capas de tela apiladas, destacando el “Derecho de la tela” y el “Revés de la tela”. Las flechas indican la “Dirección de la tela” a lo largo de las capas. Cada capa está etiquetada con “dirección del tejido”, “derecho del tejido” y “revés del tejido”. Es crucial extender la capa de tela según el largo del trazo y determinar cuál es el derecho de la tela para asegurar que todas las piezas se corten en la misma orientación, manteniendo la consistencia en la apariencia y el comportamiento del tejido en la prenda final. "}})])]),_c('div',{staticClass:"row borde-redondo-tmorada",attrs:{"titulo":"Extendido  <em>zigzag</em>"}},[_c('div',{staticClass:"col-md-4 mb-4 mb-md-0"},[_c('p',{staticClass:"p-5"},[_vm._v("El proceso es igual al extendido cara a cara, la diferencia es que no se cortan los extremos. Una vez llegado al extremo dobla sobre sí mismo para hacer el recorrido en sentido contrario. De esta forma las capas de tela deben quedar pares cara con cara y revés con revés.")])]),_c('div',{staticClass:"col-md-8"},[_c('img',{staticClass:"p-5",attrs:{"src":require("@/assets/curso/temas/tema2/tarjeta7/imagen4.svg"),"alt":"La figura expone un diagrama en blanco y negro de varias capas de tela apiladas, destacando las etiquetas de “Derecho de la tela” y “Revés de la tela”. Las flechas indican la “Dirección de la tela” y señalan que las capas están dobladas, lo que también se señala con la etiqueta “doblez de tela” en una de las capas. Cada capa está etiquetada con “dirección del tejido”, “derecho del tejido” y “revés del tejido”. Es crucial extender la capa de tela según el largo del trazo y determinar cuál es el derecho de la tela para asegurar que todas las piezas se corten en la misma orientación, manteniendo la consistencia en la apariencia y el comportamiento del tejido en la prenda final. "}})])]),_c(